.services {

    .services-title {  
        color: $gray-1;
        padding-bottom: rem(20px); }   

    .fas { 
        color: $blue-4;
        font-size: em(20px); 
    }

    & .service-title {
        color: $green-1;
        font-weight: bold;
        padding: em(20px);
        text-align: left;
    }
}

.featured {

    margin: em(30px) 0;

    .card { 
        cursor: pointer;
        border-width: 0 none;
        box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12); 

        &:hover { 
            transition: .3 ease-in; 
            box-shadow: 0 8px 17px 0 rgba(0,0,0,0.2),
                0 6px 20px 0 rgba(0,0,0,0.19); 
            transition: box-shadow 0.3s ease-in-out;
        }
    } 

    .card-title { 
        color: $green-1;
        font-weight: bold; 
    }

    .card-text { padding: em(15px); }

    .card-block {
        .btn { margin-top: em(10px); }
        a:hover, a:focus { 
            text-decoration: none;  
            border: 1px solid #007bff;
        }

    }
}